import { Grid, Typography, TextField, Button, Collapse } from "@material-ui/core"
import { withStyles, makeStyles } from "@material-ui/core/styles"
import SendIcon from '@material-ui/icons/Send';
import {useState, useEffect } from "react";
import { Redirect} from "react-router-dom";


const useStyles = makeStyles({
    multilineColor: {
      color: 'white'
    },
  });

const greenTextFieldStyles = withStyles({
    root: {
      '& label.Mui-focused': {
        color: '#1DA1F2',
        fontSize: '1rem'
      },
      '& label': {
        color: '#1DA1F2',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'red',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: '#1DA1F2',
        },
        '&:hover fieldset': {
          borderColor: 'grey',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'white',
        },
      }, 
    },
  });

function Tweet() {
    const [errorMsg, setErrorMsg] = useState("");
    const [tweetText, setTweetText] = useState("");
    const [tweetLink, setTweetLink] = useState("")
    const classes = useStyles()

    const [GreenTextField, setGreenTextField] = useState(TextField);
    useEffect(() => {
        setGreenTextField(greenTextFieldStyles(TextField));
    }, []);
    

    const postTweet = async (e) => {
        e.preventDefault();
        if (!tweetText.toLowerCase().includes("dupan!")) {
            setErrorMsg("Tweet wajib berisi keyword 'dupan!'");
        }else {
            const res = await fetch("https://dualapan.herokuapp.com/api/tweet", {
                method: "POST",
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({'text': tweetText})
            });
            if (res.ok) {
                if (errorMsg !== "") setErrorMsg("");
                const data = await res.json();
                setTweetLink(data.link);
            } else {
                setErrorMsg("Failed to upload tweet.");
            }
        }
    }

    return (
        <form noValidate autoComplete="off" onSubmit={postTweet}>
            {tweetLink !== "" && window.location.replace(tweetLink)}
            <Grid container spacing={1}>
                <Grid item xs={12} align="center">
                    <Typography variant="h3" component="h3" className="title" fontWeight="fontWeightBold">
                        Tweet @28FESS
                    </Typography>
                </Grid>
                <Grid item xs={12} align="center">
                    <GreenTextField multiline rows={4} variant="outlined" style={{width: '100%', color: 'white'}} 
                    value={tweetText}
                    onChange={(e) => setTweetText(e.target.value)}
                    error={errorMsg !== ''}
                    label="Tweet Text"
                    helperText={ errorMsg }
                    InputProps={{
                        className: classes.multilineColor
                      }}
                    />
                </Grid>
                <Grid item xs={12} align="center">
                    <Button 
                    type="submit" 
                    variant="contained" 
                    color="primary" 
                    style={{width: "100%"}}
                    >
                        <SendIcon style={{paddingRight: "20px"}}/>Post Tweet</Button>
                </Grid>
            </Grid>
        </form>
    )
}

export default Tweet
